import {mapState, mapMutations} from 'vuex'; 
export default{
	data(){
		return {
			myData:{
				nickName:'',
				gender:'',
				code:'',
				avatarUrl:'',
				sessionId:''
			},
			telephone:'',
			checkCode:'',
			codeTime:0
		}
	},
	onLoad() {
		this.$refs.popup.open()
	},
	computed:{
		...mapState(['hasLogin'])
	}, 
	methods:{
		...mapMutations(['login']),
		loginUser(){
			uni.getProvider({
			  service: 'oauth',
			  success: (res) => {
				if (~res.provider.indexOf('weixin')) {
					uni.login({
						provider: 'weixin',
						success: (res) => {
							this.myCode = res.code;
							uni.getUserInfo({
								provider: 'weixin',
								success: (info) => {//这里请求接口
									this.myData.code = res.code
									this.myData.nickName = info.userInfo.nickName
									this.myData.gender = info.userInfo.gender
									this.myData.avatarUrl = info.userInfo.avatarUrl
									console.log(this.myData)
									uni.request({
										url:'https://www.nssnail.com/wx/onLogin',
										method:'POST',
										withCredentials:true,
										data:{
											myData:this.myData,
											avatarUrl:this.myData.avatarUrl,
											code:this.myData.code,
											gender:this.myData.gender,
											nickName:this.myData.nickName
										},
										success: (res) => {
											if(res.data.stat==1){
												this.myData.sessionId = res.data.data.sessionId
												this.login(this.myData)
												if(res.data.data.isSelectedRedCross == true){
													uni.switchTab({
														url:'../my/my'
													})
												}else{
													uni.navigateTo({
														url:'../certification/certification'
													})
												}
											}else{
												uni.showToast({
													title:res.data.message
												})
											}
										}
									})
								},
								fail: () => {
									uni.showToast({title:"微信登录授权失败",icon:"none"});
								}
							})
						},
						fail: () => {
							uni.showToast({title:"微信登录授权失败",icon:"none"});
						}
					})
				}else{
					uni.showToast({
						title: '请先安装微信或升级版本',
						icon:"none"
					});
				}
			  }
			});
		},
		
		// 阻止关闭
		stopClose(){
			this.$refs.popup.open()
		}
	}
}
